<template>
  <div class="hello">
    <h1 class="title">Prueba de Independecia Poker</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad numeros aleatorios</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad_num" :disabled="true" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Numeros pseudoaleatorios (ingresar numeros separados por ,)</p>
      </div>
      <div class="column">
        <textarea
          class="textarea has-fixed-size"
          placeholder="1, 2, 3, 4, ...."
          v-model="list_num_random"
          :disabled="show"
        ></textarea>
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad decimales</p>
      </div>
      <div class="column">
        <div class="control">
          <div class="select">
            <select v-model.number="cantidad_dec" :disabled="show">
              <option>3</option>
              <option>4</option>
              <option>5</option>
            </select>
          </div>
        </div>
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Confianza</p>
      </div>
      <div class="column">
        <div class="control">
          <div class="select">
            <select v-model.number="confianza" :disabled="show">
              <option>95</option>
              <option>99</option>
              <option>90</option>
              <option>98</option>
              <option>80</option>
            </select>
          </div>
        </div>
        <!-- <input class="input" type="number" v-model.number="confianza" :disabled="true" /> -->
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="show">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>
    <div class="columns is-gapless results" v-if="show && !show_error">
      <div class="column is-1"></div>
      <div class="column is-2">
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>X</th>
              <th>Numero</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(element, index) in num_random" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ element }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-1"></div>
      <div class="column">
        <div class="columns is-gapless">
          <div class="column">
            <table class="table is-hoverable">
              <thead>
                <tr>
                  <th>Categoria</th>
                  <th>Oi</th>
                  <th>Prob</th>
                  <th>Ei</th>
                  <th>(Ei - Oi)^2 / Ei</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(element, index) in categorias" :key="index">
                  <td>{{ element }}</td>
                  <td>{{ valores_Oi[index] }}</td>
                  <td>{{ probabilidades[index] }}</td>
                  <td>{{ valores_Ei[index] }}</td>
                  <td>{{ valores_formula[index] }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Alfa</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="alfa" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor X^2</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="total_chi_cuadrada" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor X^2 Tabla</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="chi_cuadrada" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Ho</p>
          </div>
          <div class="column">
            <p>Los numeros generados SON independientes</p>
            <p>X^2 Calc. &#60; X^2 Tabla</p>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Hi</p>
          </div>
          <div class="column">
            <p>Los numeros generados NO SON independientes</p>
            <p>X^2 Calc. &#62; X^2 Tabla</p>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Conclusion</p>
          </div>
          <div class="column">
            <p
              v-if="total_chi_cuadrada < chi_cuadrada"
            >{{ total_chi_cuadrada }} &#60; {{ chi_cuadrada }}</p>
            <p v-if="total_chi_cuadrada < chi_cuadrada">No se rechaza Ho</p>
            <p
              v-if="total_chi_cuadrada > chi_cuadrada"
            >{{ total_chi_cuadrada }} &#62; {{ chi_cuadrada }}</p>
            <p v-if="total_chi_cuadrada > chi_cuadrada">Se rechaza Ho</p>
          </div>
        </div>
      </div>
      <div class="column is-1"></div>
    </div>
    <div class="columns is-gapless results" v-if="show_error">
      <div class="column is-3"></div>
      <div class="column">
        <h1 class="title">Error en los datos, puede ser por:</h1>
        <div class="content is-medium">
          <ul>
            <li>Numeros mayores de 1</li>
            <li>Mal formato de numeros</li>
          </ul>
        </div>
      </div>
      <div class="column is-3"></div>
    </div>
  </div>
</template>

<script>
import formula from "inv-chisquare-cdf";
export default {
  name: "Poker",
  props: {
    msg: String
  },
  data() {
    return {
      list_num_random: "",
      cantidad_num: 0,
      cantidad_dec: 4,
      num_random: [],
      categorias: [],
      probabilidades: [],
      total_chi_cuadrada: 0,
      chi_cuadrada: 0,
      confianza: 95,
      valor_z: 1.96,
      alfa: 0,
      valores_Oi: [],
      valores_Ei: [],
      valores_formula: [],
      show: false,
      show_error: false
    };
  },
  computed: {},

  methods: {
    calc() {
      this.show = true;

      this.num_random = this.list_num_random.split(",").map(Number);
      this.cantidad_num = this.num_random.length;

      for (let i = 0; i < this.cantidad_num; i++) {
        if (isNaN(this.num_random[i])) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
        if (this.num_random[i] > 1) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
      }

      switch (this.confianza) {
        case 99:
          this.valor_z = 2.58;
          this.alfa = 0.005 * 2;
          break;
        case 90:
          this.valor_z = 1.645;
          this.alfa = 0.05 * 2;
          break;
        case 98:
          this.valor_z = 2.33;
          this.alfa = 0.01 * 2;
          break;
        case 80:
          this.valor_z = 1.28;
          this.alfa = 0.1 * 2;
          break;

        default:
          this.valor_z = 1.96;
          this.alfa = 0.025 * 2;
      }

      if (this.cantidad_dec == 3) {
        this.categorias[0] = "Todos Diferentes";
        this.categorias[1] = "Un Par";
        this.categorias[2] = "Tercia";
      } else if (this.cantidad_dec == 4) {
        this.categorias[0] = "Todos Diferentes";
        this.categorias[1] = "Un Par";
        this.categorias[2] = "Dos Pares";
        this.categorias[3] = "Tercia";
        this.categorias[4] = "Poker";
      } else {
        this.categorias[0] = "Todos Diferentes";
        this.categorias[1] = "Un Par";
        this.categorias[2] = "Dos Pares";
        this.categorias[3] = "Tercia";
        this.categorias[4] = "Una Tercia y Un Par";
        this.categorias[5] = "Poker";
        this.categorias[6] = "Quintilla";
      }

      this.chi_cuadrada = formula
        .invChiSquareCDF(1 - this.alfa, this.categorias.length - 1)
        .toFixed(this.cantidad_dec);

      let count_dif = 0;
      let count_un_par = 0;
      let count_dos_par = 0;
      let count_tercia = 0;
      let count_tercia_par = 0;
      let count_poker = 0;
      let count_quintilla = 0;
      let cantidad_pares = 0;
      let cantidad_tercias = 0;

      for (let i = 0; i < this.cantidad_num; i++) {
        let number_string = this.num_random[i]
          .toString()
          .replace("0.", "")
          .split("");

        for (let e = number_string.length; e < this.cantidad_dec; e++) {
          number_string.push("0");
        }
        let count = 0;
        let max_count = 0;
        for (let number = 0; number < 10; number++) {
          for (let j = 0; j <= number_string.length - 1; j++) {
            if (number_string[j] == number) {
              count++;
            }
          }

          if (count == 2) {
            cantidad_pares++;
          }
          if (count == 3) {
            cantidad_tercias++;
          }

          if (count > max_count) {
            max_count = count;
          }
          count = 0;
        }

        if (this.cantidad_dec == 3) {
          if (max_count == 3) {
            count_tercia++;
          } else if (max_count == 2) {
            count_un_par++;
          } else if (max_count == 1) {
            count_dif++;
          }
          this.valores_Oi[0] = count_dif;
          this.valores_Oi[1] = count_un_par;
          this.valores_Oi[2] = count_tercia;
          this.probabilidades[0] = 0.72;
          this.probabilidades[1] = 0.27;
          this.probabilidades[2] = 0.01;
        } else if (this.cantidad_dec == 4) {
          if (max_count == 4) {
            count_poker++;
          } else if (max_count == 3) {
            count_tercia++;
          } else if (max_count == 2 && cantidad_pares == 1) {
            count_un_par++;
          } else if (max_count == 2 && cantidad_pares == 2) {
            count_dos_par++;
          } else if (max_count == 1) {
            count_dif++;
          }
          this.valores_Oi[0] = count_dif;
          this.valores_Oi[1] = count_un_par;
          this.valores_Oi[2] = count_dos_par;
          this.valores_Oi[3] = count_tercia;
          this.valores_Oi[4] = count_poker;
          this.probabilidades[0] = 0.504;
          this.probabilidades[1] = 0.432;
          this.probabilidades[2] = 0.027;
          this.probabilidades[3] = 0.036;
          this.probabilidades[4] = 0.001;
        } else if (this.cantidad_dec == 5) {
          if (max_count == 5) {
            count_quintilla++;
          } else if (max_count == 4) {
            count_poker++;
          } else if (cantidad_tercias == 1 && cantidad_pares == 1) {
            count_tercia_par++;
          } else if (max_count == 3) {
            count_tercia++;
          } else if (max_count == 2 && cantidad_pares == 1) {
            count_un_par++;
          } else if (max_count == 2 && cantidad_pares == 2) {
            count_dos_par++;
          } else if (max_count == 1) {
            count_dif++;
          }
          this.valores_Oi[0] = count_dif;
          this.valores_Oi[1] = count_un_par;
          this.valores_Oi[2] = count_dos_par;
          this.valores_Oi[3] = count_tercia;
          this.valores_Oi[4] = count_tercia_par;
          this.valores_Oi[5] = count_poker;
          this.valores_Oi[6] = count_quintilla;
          this.probabilidades[0] = 0.3024;
          this.probabilidades[1] = 0.504;
          this.probabilidades[2] = 0.108;
          this.probabilidades[3] = 0.009;
          this.probabilidades[4] = 0.072;
          this.probabilidades[5] = 0.0045;
          this.probabilidades[6] = 0.0001;
        }
        cantidad_pares = 0;
        cantidad_tercias = 0;
      }

      let acomulado = 0;

      for (let i = 0; i < this.categorias.length; i++) {
        this.valores_Ei[i] = (
          this.probabilidades[i] * this.cantidad_num
        ).toFixed(4);
        this.valores_formula[i] = (
          Math.pow(this.valores_Ei[i] - this.valores_Oi[i], 2) /
          this.valores_Ei[i]
        ).toFixed(4);

        acomulado += parseFloat(this.valores_formula[i]);
      }

      this.total_chi_cuadrada = acomulado.toFixed(4);

      return 0;
    },

    newCalc() {
      this.cantidad_num = 0;
      this.cantidad_dec = 4;
      this.num_random = [];
      this.categorias = [];
      this.probabilidades = [];
      this.total_chi_cuadrada = 0;
      this.chi_cuadrada = 0;
      this.confianza = 95;
      this.valor_z = 1.96;
      this.alfa = 0;
      this.valores_Oi = [];
      this.valores_Ei = [];
      this.valores_formula = [];
      this.show = false;
      this.show_error = false;
      this.list_num_random = "";
    }
  }
};
</script>

