<template>
  <div class="hello">
    <h1 class="title">Prueba de Series</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad numeros aleatorios</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad_num" :disabled="true" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Numeros pseudoaleatorios (ingresar numeros separados por ,)</p>
      </div>
      <div class="column">
        <textarea
          class="textarea has-fixed-size"
          placeholder="1, 2, 3, 4, ...."
          v-model="list_num_random"
          :disabled="show"
        ></textarea>
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Numero de cuadrantes de la grafica</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="divisiones" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Confianza</p>
      </div>
      <div class="column">
        <div class="control">
          <div class="select">
            <select v-model.number="confianza" :disabled="show">
              <option>95</option>
              <option>99</option>
              <option>90</option>
              <option>98</option>
              <option>80</option>
            </select>
          </div>
        </div>
        <!-- <input class="input" type="number" v-model.number="confianza" :disabled="true" /> -->
      </div>
      <div class="column is-2"></div>
    </div>
    <button class="button is-info" v-on:click="calc()" :disabled="show || calc_div">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>
    <div class="columns is-gapless results" v-if="show && !show_error">
      <div class="column is-1"></div>
      <div class="column is-2">
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>X</th>
              <th>Numero</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(element, index) in num_random" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ element }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-1"></div>
      <div class="column is-2">
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>Coor.</th>
              <th>X</th>
              <th>Y</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(element, index) in coor_valores" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ element.valor_x }}</td>
              <td>{{ element.valor_y }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-1"></div>
      <div class="column">
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>Cuadrante</th>
              <th>X</th>
              <th>Y</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="index in divisiones" :key="index">
              <td>{{index}}</td>
              <td>{{cuadrantes[index-1].x_menor}} - {{cuadrantes[index-1].x_mayor}}</td>
              <td>{{cuadrantes[index-1].y_menor}} - {{cuadrantes[index-1].y_mayor}}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-1"></div>
    </div>
    <div class="columns is-gapless results" v-if="show && !show_error">
      <div class="column is-1"></div>
      <div class="column">
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>Intervalo</th>
              <th>Oi</th>
              <th>Ei</th>
              <th>X^2</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="index in divisiones" :key="index">
              <td>{{index}}</td>
              <td>{{valores_Oi[index-1]}}</td>
              <td>{{valor_iE}}</td>
              <td>{{valores_X2[index-1]}}</td>
            </tr>
          </tbody>
        </table>
        <div class="columns is-gapless">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>X^2 Calc.</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="valor_total_X2" :disabled="show" />
          </div>
          <div class="column is-2"></div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>X^2 Tabla</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="chi_cuadrada" :disabled="show" />
          </div>
          <div class="column is-2"></div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Ho</p>
          </div>
          <div class="column">
            <p>Los numeros generados SON independientes</p>
            <p>X^2 Calc. &#60; X^2 Tabla</p>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Hi</p>
          </div>
          <div class="column">
            <p>Los numeros generados NO SON independientes</p>
            <p>X^2 Calc. &#62; X^2 Tabla</p>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Conclusion</p>
          </div>
          <div class="column">
            <p v-if="valor_total_X2 < chi_cuadrada">{{ valor_total_X2 }} &#60; {{ chi_cuadrada }}</p>
            <p v-if="valor_total_X2 < chi_cuadrada">No se rechaza Ho</p>
            <p v-if="valor_total_X2 > chi_cuadrada">{{ valor_total_X2 }} &#62; {{ chi_cuadrada }}</p>
            <p v-if="valor_total_X2 > chi_cuadrada">Se rechaza Ho</p>
          </div>
        </div>
      </div>
      <div class="column is-1"></div>
    </div>
    <div class="columns is-gapless results" v-if="show_error">
      <div class="column is-3"></div>
      <div class="column">
        <h1 class="title">Error en los datos, puede ser por:</h1>
        <div class="content is-medium">
          <ul>
            <li>Numeros mayores de 1</li>
            <li>Mal formato de numeros</li>
          </ul>
        </div>
      </div>
      <div class="column is-3"></div>
    </div>
  </div>
</template>

<script>
// libreria instalada para calcular el valor de chi cuadrada en la tabla
import formula from "inv-chisquare-cdf";
export default {
  name: "Series",
  props: {
    msg: String
  },
  data() {
    return {
      list_num_random: "",
      cantidad_num: 0,
      num_random: [],
      coor_valores: [],
      cuadrantes: [],
      cantidad_por_cuadrante: [],
      divisiones: 0,
      confianza: 95,
      valor_z: 1.96,
      valores_Oi: [],
      valores_X2: [],
      valor_total_X2: 0,
      valor_iE: 0,
      chi_cuadrada: 0,
      alfa: 0,
      show: false,
      show_error: false
    };
  },
  computed: {
    calc_div() {
      let raiz = Math.sqrt(this.divisiones);
      if (this.divisiones % raiz == 0) {
        return false;
      }
      return true;
    }
  },

  methods: {
    calc() {
      this.show = true;
      this.num_random = this.list_num_random.split(",").map(Number);
      this.cantidad_num = this.num_random.length;
      this.valor_iE = ((this.cantidad_num - 1) / this.divisiones).toFixed(2);

      for (let i = 0; i < this.cantidad_num; i++) {
        if (isNaN(this.num_random[i])) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
        if (this.num_random[i] > 1) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
      }

      // for (let i = 0; i < this.cantidad_num; i++) {
      //   this.num_random[i] = parseFloat(
      //     (Math.random() * (0.9999 - 0.0001) + 0.0001).toFixed(4)
      //   );
      // }

      switch (this.confianza) {
        case 99:
          this.valor_z = 2.58;
          this.alfa = 0.005;
          break;
        case 90:
          this.valor_z = 1.645;
          this.alfa = 0.05;
          break;
        case 98:
          this.valor_z = 2.33;
          this.alfa = 0.01;
          break;
        case 80:
          this.valor_z = 1.28;
          this.alfa = 0.1;
          break;

        default:
          this.valor_z = 1.96;
          this.alfa = 0.025;
      }

      this.chi_cuadrada = formula
        .invChiSquareCDF(1 - this.alfa * 2, this.divisiones - 1)
        .toFixed(4);

      console.log(this.chi_cuadrada);

      for (let i = 0; i < this.cantidad_num - 1; i++) {
        let new_rango = {
          valor_x: this.num_random[i],
          valor_y: this.num_random[i + 1]
        };
        this.coor_valores.push(new_rango);
      }
      let raiz = Math.sqrt(this.divisiones);
      let coor_x = 0;
      let coor_y = 0;

      for (let i = 0; i < raiz; i++) {
        let aumento = 1 / raiz;

        for (let j = 0; j < raiz; j++) {
          let new_cuadrante = {
            x_menor: coor_x.toFixed(4),
            x_mayor: (coor_x + aumento).toFixed(4),
            y_menor: coor_y.toFixed(4),
            y_mayor: (coor_y + aumento).toFixed(4)
          };
          this.cuadrantes.push(new_cuadrante);

          coor_y += aumento;
        }
        coor_x += aumento;
        coor_y = 0;
      }

      for (let i = 0; i < this.cuadrantes.length; i++) {
        for (let j = 0; j < this.coor_valores.length; j++) {
          if (
            this.coor_valores[j].valor_x > this.cuadrantes[i].x_menor &&
            this.coor_valores[j].valor_x < this.cuadrantes[i].x_mayor &&
            this.coor_valores[j].valor_y > this.cuadrantes[i].y_menor &&
            this.coor_valores[j].valor_y < this.cuadrantes[i].y_mayor
          ) {
            this.cantidad_por_cuadrante.push(i + 1);
          }
        }
      }

      for (let i = 0; i < this.divisiones; i++) {
        this.valores_Oi.push(0);
      }

      for (let i = 0; i < this.cantidad_por_cuadrante.length; i++) {
        this.valores_Oi[this.cantidad_por_cuadrante[i] - 1]++;
      }

      for (let i = 0; i < this.divisiones; i++) {
        this.valores_X2[i] = parseFloat(
          (
            Math.pow(this.valor_iE - this.valores_Oi[i], 2) / this.valor_iE
          ).toFixed(4)
        );
        this.valor_total_X2 = this.valor_total_X2 + this.valores_X2[i];
      }
      return 0;
    },

    newCalc() {
      this.show = false;
      this.show_error = false;
      this.cantidad_num = 0;
      this.num_random = [];
      this.divisiones = 0;
      this.confianza = 95;
      this.valor_z = 1.96;
      this.alfa = 0;
      this.coor_valores = [];
      this.cuadrantes = [];
      this.cantidad_por_cuadrante = [];
      this.valores_Oi = [];
      this.valores_X2 = [];
      this.valor_iE;
      this.valor_total_X2 = 0;
      this.chi_cuadrada = 0;
      this.list_num_random = "";
    }
  }
};
</script>

