<template>
  <div class="hello">
    <h1 class="title">Prueba Varianza</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad numeros aleatorios</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad_num" :disabled="true" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Numeros pseudoaleatorios (ingresar numeros separados por ,)</p>
      </div>
      <div class="column">
        <textarea
          class="textarea has-fixed-size"
          placeholder="1, 2, 3, 4, ...."
          v-model="list_num_random"
          :disabled="show"
        ></textarea>
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Confianza</p>
      </div>
      <div class="column">
        <div class="control">
          <div class="select">
            <select v-model.number="confianza" :disabled="show">
              <option>95</option>
              <option>99</option>
              <option>90</option>
              <option>98</option>
              <option>80</option>
            </select>
          </div>
        </div>
        <!-- <input class="input" type="number" v-model.number="confianza" :disabled="true" /> -->
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="show">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>
    <div class="columns is-gapless results" v-if="show && !show_error">
      <div class="column is-1"></div>
      <div class="column is-2">
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>X</th>
              <th>Numero</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(element, index) in num_random" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ element }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-1"></div>
      <div class="column">
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Varianza</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="varianza" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
