              v-if="varianza < limiteSup && varianza > limiteInf"
            >{{limiteInf}} &#60; {{varianza}} &#60; {{limiteSup}}</p>
            <p v-if="varianza < limiteSup && varianza > limiteInf">No se rechaza Ho</p>
            <p v-if="varianza > limiteSup ">{{varianza}} &#62; {{limiteSup}}</p>
            <p v-if="varianza > limiteSup ">Se rechaza Ho</p>
            <p v-if="varianza < limiteInf">{{varianza}} &#60; {{limiteInf}}</p>
            <p v-if="varianza < limiteInf">Se rechaza Ho</p>
