      <div class="column">
        <h1 class="title">Error en los datos, puede ser por:</h1>
        <div class="content is-medium">
          <ul>
            <li>Numeros mayores de 1</li>
            <li>Mal formato de numeros</li>
          </ul>
        </div>
      </div>
      <div class="column is-3"></div>
    </div>
  </div>
</template>

<script>
import formula from "inv-chisquare-cdf";

export default {
  name: "Algo_lineal",
  props: {
    msg: String
  },
  data() {
    return {
      list_num_random: "",
      cantidad_num: 0,
      num_random: [],
      varianza: 0,
      confianza: 95,
      limiteInf: 0,
      limiteSup: 0,
      alfa: 0,
      chi_cuadrada: 0,
      menos_chi_cuadrada: 0,
      show: false,
      show_error: false
    };
  },

  methods: {
    calc() {
      this.show = true;

      this.num_random = this.list_num_random.split(",").map(Number);
      this.cantidad_num = this.num_random.length;

      for (let i = 0; i < this.cantidad_num; i++) {
        if (isNaN(this.num_random[i])) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
        if (this.num_random[i] > 1) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
      }

      let promedio = (
        this.num_random.reduce((previous, current) => (current += previous)) /
        this.cantidad_num
      ).toFixed(4);
      let acumulado = 0;
      for (let e = 0; e < this.cantidad_num; e++) {
        let cuadrado = Math.pow(this.num_random[e] - promedio, 2);
        acumulado = acumulado + cuadrado;
      }
      this.varianza = (acumulado / (this.cantidad_num - 1)).toFixed(4);
      this.alfa = ((1 - this.confianza / 100) / 2).toFixed(4);

      this.chi_cuadrada = formula
        .invChiSquareCDF(1 - this.alfa, this.cantidad_num - 1)
        .toFixed(4);
      this.menos_chi_cuadrada = formula
        .invChiSquareCDF(this.alfa, this.cantidad_num - 1)
        .toFixed(4);

      this.limiteSup = (
        this.chi_cuadrada /
        (12 * (this.cantidad_num - 1))
      ).toFixed(4);
      this.limiteInf = (
        this.menos_chi_cuadrada /
        (12 * (this.cantidad_num - 1))
      ).toFixed(4);
      //   let alfa = ((1 - this.confianza / 100) / 2).toFixed(4);

      return 0;
    },
    newCalc() {
      this.show = false;
      this.cantidad_num = 0;
      this.num_random = [];
      this.list_num_random = "";
      this.varianza = 0;
      this.confianza = 95;
      this.limiteInf = 0;
      this.limiteSup = 0;
      this.alfa = 0;
      this.chi_cuadrada = 0;
      this.menos_chi_cuadrada = 0;
      this.show_error = false;
    }
  }
};
</script>

