<template>
  <div class="hello">
    <h1 class="title">Transformada Inversa - Bernoulli</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad numeros aleatorios</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad_num" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Probabiliad Fallo (1-100)</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="probabilidad_falla" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Probabiliad NO Fallo (1-100)</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="probabilidad_buena" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="show || valid_data">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>

    <div class="columns is-gapless results" v-if="show">
      <div class="column is-1"></div>
      <div class="column">
        <div class="columns is-gapless is-vcentered">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>Distribucion Bernoulli</p>
          </div>
          <div class="column">
            <img src="../../assets/bernoulli/formula.png" />
          </div>
          <div class="column is-2"></div>
        </div>
        <div class="columns is-gapless is-vcentered">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>Distribucion Bernoulli Xi</p>
          </div>
          <div class="column">
            <img src="../../assets/bernoulli/valor_xi.png" />
          </div>
          <div class="column is-2"></div>
        </div>

        <div class="columns is-gapless is-vcentered">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>0</p>
          </div>
          <div class="column">
            <input class="input" type="text" v-model="value_0" :disabled="true" />
          </div>
          <div class="column is-2"></div>
        </div>

        <div class="columns is-gapless is-vcentered">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>1</p>
          </div>
          <div class="column">
            <input class="input" type="text" v-model="value_1" :disabled="true" />
          </div>
          <div class="column is-2"></div>
        </div>
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>Medición</th>
              <th>Ri</th>
              <th>Xi</th>
              <th>Evento Maquina</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(element, index) in num_random" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ element }}</td>
              <td>{{ valores_Xi[index] }}</td>
              <td v-if="valores_Xi[index] ==0">No hay falla</td>
              <td v-if="valores_Xi[index] ==1">Si hay falla</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-1"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Bernoulli",
  props: {
    msg: String
  },
  data() {
    return {
      cantidad_num: 0,
      num_random: [],
      valores_Xi: [],
      probabilidad_buena: 0,
      probabilidad_falla: 0,
      value_0: "",
      value_1: "",
      falla: 0,
      show: false
    };
  },
  computed: {
    valid_data() {
      if (this.probabilidad_buena + this.probabilidad_falla == 100) {
        return false;
      }
      return true;
    }
  },

  methods: {
    calc() {
      this.show = true;

      this.falla = 1 - this.probabilidad_falla / 100;

      for (let i = 0; i < this.cantidad_num; i++) {
        this.num_random[i] = parseFloat(
          (Math.random() * (0.9999 - 0.0001) + 0.0001).toFixed(4)
        );
        if (this.num_random[i] > this.falla) {
          this.valores_Xi[i] = 1;
        } else {
          this.valores_Xi[i] = 0;
        }
      }

      this.value_0 = `0 - ${this.falla}`;
      this.value_1 = `${this.falla} - 1`;

      return 0;
    },
    newCalc() {
      this.show = false;
      this.cantidad_num = 0;
      this.num_random = [];
      this.valores_Xi = [];
      this.probabilidad_buena = 0;
      this.probabilidad_falla = 0;
      this.value_0 = "";
      this.value_1 = "";
      this.falla = 0;
      this.media = 0;
    }
  }
};
</script>

