<template>
  <div class="hello">
    <h1 class="title">Prueba de Independecia Arriba y Abajo</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad numeros aleatorios</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad_num" :disabled="true" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Numeros pseudoaleatorios (ingresar numeros separados por ,)</p>
      </div>
      <div class="column">
        <textarea
          class="textarea has-fixed-size"
          placeholder="1, 2, 3, 4, ...."
          v-model="list_num_random"
          :disabled="show"
        ></textarea>
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Confianza</p>
      </div>
      <div class="column">
        <div class="control">
          <div class="select">
            <select v-model.number="confianza" :disabled="show">
              <option>95</option>
              <option>99</option>
              <option>90</option>
              <option>98</option>
              <option>80</option>
            </select>
          </div>
        </div>
        <!-- <input class="input" type="number" v-model.number="confianza" :disabled="true" /> -->
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="show">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>
    <div class="columns is-gapless results" v-if="show && !show_error">
      <div class="column is-1"></div>
      <div class="column is-2">
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>X</th>
              <th>Numero</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(element, index) in num_random" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ element }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-1"></div>
      <div class="column is-2">
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>Index</th>
              <th>S</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(element, index) in valores_S" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ element }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-1"></div>
      <div class="column">
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Alfa</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="alfa" :disabled="true" />
          </div>
        </div>

        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor Co</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="valor_Co" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor 𝜇𝐶𝑜</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="valor_Media_o" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor σ2𝐶𝑜</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="valor_Varianza_o" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor 𝑍𝑜</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="valor_Zo" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor Z Tabla</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="valor_z" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Ho</p>
          </div>
          <div class="column">
            <p>Los numeros generados SON independientes</p>
            <p>𝑍𝑜 &#60; Z Tabla</p>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Hi</p>
          </div>
          <div class="column">
            <p>Los numeros generados NO SON independientes</p>
            <p>𝑍𝑜 &#62; Z Tabla</p>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Conclusion</p>
          </div>
          <div class="column">
            <p v-if="valor_Zo < valor_z">{{ valor_Zo }} &#60; {{ valor_z }}</p>
            <p v-if="valor_Zo < valor_z">No se rechaza Ho</p>
            <p v-if="valor_Zo > valor_z">{{ valor_Zo }} &#62; {{ valor_z }}</p>
            <p v-if="valor_Zo > valor_z">Se rechaza Ho</p>
          </div>
        </div>
      </div>
      <div class="column is-1"></div>
    </div>
    <div class="columns is-gapless results" v-if="show_error">
      <div class="column is-3"></div>
      <div class="column">
        <h1 class="title">Error en los datos, puede ser por:</h1>
        <div class="content is-medium">
          <ul>
            <li>Numeros mayores de 1</li>
            <li>Mal formato de numeros</li>
          </ul>
        </div>
      </div>
      <div class="column is-3"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Arriba_Abajo",
  props: {
    msg: String
  },
  data() {
    return {
      list_num_random: "",
      cantidad_num: 0,
      num_random: [],
      confianza: 95,
      valor_z: 1.96,
      alfa: 0,
      valores_S: [],
      valor_Co: 1,
      valor_Media_o: 0,
      valor_Varianza_o: 0,
      valor_Zo: 0,
      show: false,
      show_error: false
    };
  },
  computed: {},

  methods: {
    calc() {
      this.show = true;

      this.num_random = this.list_num_random.split(",").map(Number);
      this.cantidad_num = this.num_random.length;

      for (let i = 0; i < this.cantidad_num; i++) {
        if (isNaN(this.num_random[i])) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
        if (this.num_random[i] > 1) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
      }

      switch (this.confianza) {
        case 99:
          this.valor_z = 2.58;
          this.alfa = 0.005;
          break;
        case 90:
          this.valor_z = 1.645;
          this.alfa = 0.05;
          break;
        case 98:
          this.valor_z = 2.33;
          this.alfa = 0.01;
          break;
        case 80:
          this.valor_z = 1.28;
          this.alfa = 0.1;
          break;

        default:
          this.valor_z = 1.96;
          this.alfa = 0.025;
      }

      for (let i = 1; i < this.cantidad_num; i++) {
        if (this.num_random[i - 1] > this.num_random[i]) {
          this.valores_S.push(0);
        } else {
          this.valores_S.push(1);
        }
      }

      for (let i = 0; i < this.valores_S.length - 1; i++) {
        if (this.valores_S[i] != this.valores_S[i + 1]) {
          this.valor_Co++;
        }
      }

      this.valor_Media_o = ((2 * this.cantidad_num - 1) / 3).toFixed(4);
      this.valor_Varianza_o = ((16 * this.cantidad_num - 29) / 90).toFixed(4);
      this.valor_Zo = Math.abs(
        (this.valor_Co - this.valor_Media_o) / Math.sqrt(this.valor_Varianza_o)
      ).toFixed(4);

      return 0;
    },
    newCalc() {
      this.cantidad_num = 0;
      this.num_random = [];
      this.confianza = 95;
      this.valor_z = 1.96;
      this.alfa = 0;
      this.valores_S = [];
      this.valor_Co = 1;
      this.valor_Media_o = 0;
      this.valor_Varianza_o = 0;
      this.valor_Zo = 0;
      this.show = false;
      this.show_error = false;
      this.list_num_random = "";
    }
  }
};
</script>

