<template>
  <div class="hello">
    <h1 class="title">Prueba de Huecos</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad numeros aleatorios</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad_num" :disabled="true" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Numeros pseudoaleatorios (ingresar numeros separados por ,)</p>
      </div>
      <div class="column">
        <textarea
          class="textarea has-fixed-size"
          placeholder="1, 2, 3, 4, ...."
          v-model="list_num_random"
          :disabled="show"
        ></textarea>
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Alfa Rango</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="alfa_rango" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Beta Rango</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="beta" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Confianza</p>
      </div>
      <div class="column">
        <div class="control">
          <div class="select">
            <select v-model.number="confianza" :disabled="show">
              <option>95</option>
              <option>99</option>
              <option>90</option>
              <option>98</option>
              <option>80</option>
            </select>
          </div>
        </div>
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="show || calc_datos">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>

    <div class="columns is-gapless results" v-if="show && !show_error">
      <div class="column is-1"></div>
      <div class="column is-2">
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>X</th>
              <th>Numero</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(element, index) in num_random" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ element }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-1"></div>
      <div class="column is-2">
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>Index</th>
              <th>S</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(element, index) in valores_S" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ element }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-1"></div>
      <div class="column">
        <div class="columns is-gapless">
          <table class="table is-hoverable">
            <thead>
              <tr>
                <th>Tamaño Hueco</th>
                <th>Oi</th>
                <th>Ei</th>
                <th>(Ei - Oi)^2 / Ei</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="( index) in 6" :key="index">
                <td v-if="index!=6">{{ index -1 }}</td>
                <td v-if="index==6">&#62;= {{ index -1 }}</td>
                <td>{{huecos[index-1]}}</td>
                <td>{{valor_Ei[index-1]}}</td>
                <td>{{valor_error[index-1]}}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>X^2 Calc.</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="error_total" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>X^2 Tabla</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="chi_cuadrada" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Ho</p>
          </div>
          <div class="column">
            <p>𝑟𝑖 ~ Independientes</p>
            <p>Z Calc. &#60; Z Tabla</p>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Hi</p>
          </div>
          <div class="column">
            <p>𝑟𝑖 ~ Dependientes</p>
            <p>Z Calc. &#62; Z Tabla</p>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Conclusion</p>
          </div>
          <div class="column">
            <p v-if="error_total < chi_cuadrada ">{{ error_total }} &#60; {{ chi_cuadrada }}</p>
            <p v-if="error_total < chi_cuadrada ">No se rechaza Ho</p>
            <p v-if="error_total > chi_cuadrada">{{ error_total }} &#62; {{ chi_cuadrada }}</p>
            <p v-if="error_total > chi_cuadrada">Se rechaza Ho</p>
          </div>
        </div>
      </div>
      <div class="column is-1"></div>
    </div>
    <div class="columns is-gapless results" v-if="show_error">
      <div class="column is-3"></div>
      <div class="column">
        <h1 class="title">Error en los datos, puede ser por:</h1>
        <div class="content is-medium">
          <ul>
            <li>Numeros mayores de 1</li>
            <li>Mal formato de numeros</li>
          </ul>
        </div>
      </div>
      <div class="column is-3"></div>
    </div>
  </div>
</template>

<script>
import formula from "inv-chisquare-cdf";
export default {
  name: "Arriba_Abajo_Media",
  props: {
    msg: String
  },
  data() {
    return {
      list_num_random: "",
      cantidad_num: 0,
      num_random: [],
      confianza: 95,
      valor_z: 1.96,
      alfa_rango: 0,
      beta: 0,
      alfa: 0,
      hueco_0: 0,
      hueco_1: 0,
      hueco_2: 0,
      hueco_3: 0,
      hueco_4: 0,
      hueco_5: 0,
      huecos: [],
      cantidad_huecos: 0,
      valor_Ei: [],
      valor_error: [],
      error_total: [],
      valores_S: [],
      chi_cuadrada: 0,
      show: false,
      show_error: false
    };
  },
  computed: {
    calc_datos() {
      if (this.alfa_rango < this.beta) {
        if (this.beta <= 1) {
          if (this.alfa_rango >= 0) {
            return false;
          }
        }
      }

      return true;
    }
  },

  methods: {
    calc() {
      this.show = true;

      this.num_random = this.list_num_random.split(",").map(Number);
      this.cantidad_num = this.num_random.length;

      for (let i = 0; i < this.cantidad_num; i++) {
        if (isNaN(this.num_random[i])) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
        if (this.num_random[i] > 1) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
      }

      switch (this.confianza) {
        case 99:
          this.valor_z = 2.58;
          this.alfa = 0.005;
          break;
        case 90:
          this.valor_z = 1.645;
          this.alfa = 0.05;
          break;
        case 98:
          this.valor_z = 2.33;
          this.alfa = 0.01;
          break;
        case 80:
          this.valor_z = 1.28;
          this.alfa = 0.1;
          break;

        default:
          this.valor_z = 1.96;
          this.alfa = 0.025;
      }

      for (let i = 0; i < this.cantidad_num; i++) {
        if (
          this.num_random[i] >= this.alfa_rango &&
          this.num_random[i] <= this.beta
        ) {
          this.valores_S.push(1);
        } else {
          this.valores_S.push(0);
        }
      }

      let copy_S = [...this.valores_S];
      let list_index_0 = [];
      let list_index_1 = [];

      for (let i = 0; i < copy_S.length; i++) {
        if (copy_S[i] == 0) {
          list_index_0.push(i);
        } else {
          list_index_1.push(i);
        }
      }

      for (let i = 1; i < list_index_1.length; i++) {
        if (list_index_1[i] == list_index_1[i - 1] + 1) {
          this.hueco_0++;
        }
      }

      console.log(list_index_0);
      let inicio = 0;
      let fin = 0;

      for (let i = 0; i < copy_S.length; i++) {
        if (copy_S[i] == 0) {
          inicio = i;
          console.log("primer cero encontraro en - " + i);
          let e = i;
          while (copy_S[e] == 0 && i < copy_S.length) {
            e++;
            i++;
          }
          fin = i - 1;
          console.log("ultimop cero encontraro en - " + (i - 1));
          i--;
          if (fin - inicio + 1 == 1) {
            this.hueco_1++;
          } else if (fin - inicio + 1 == 2) {
            this.hueco_2++;
          } else if (fin - inicio + 1 == 3) {
            this.hueco_3++;
          } else if (fin - inicio + 1 == 4) {
            this.hueco_4++;
          } else if (fin - inicio + 1 >= 5) {
            this.hueco_5++;
          }
        }
      }
      // list_index.push(this.cantidad_num - 1);
      // for (let i = 1; i < list_index.length; i++) {
      //   let cantidad_huecos = list_index[i] - list_index[i - 1] - 1;
      //   if (i == list_index.length - 1) {
      //     if (cantidad_huecos != 0) {
      //       if (cantidad_huecos == 1) {
      //         this.hueco_1++;
      //       } else if (cantidad_huecos == 2) {
      //         this.hueco_2++;
      //       } else if (cantidad_huecos == 3) {
      //         this.hueco_3++;
      //       } else if (cantidad_huecos == 4) {
      //         this.hueco_4++;
      //       } else if (cantidad_huecos >= 5) {
      //         this.hueco_5++;
      //       }
      //     }
      //   } else if (cantidad_huecos == 0) {
      //     this.hueco_0++;
      //   } else if (cantidad_huecos == 1) {
      //     this.hueco_1++;
      //   } else if (cantidad_huecos == 2) {
      //     this.hueco_2++;
      //   } else if (cantidad_huecos == 3) {
      //     this.hueco_3++;
      //   } else if (cantidad_huecos == 4) {
      //     this.hueco_4++;
      //   } else if (cantidad_huecos >= 5) {
      //     this.hueco_5++;
      //   }
      // }

      this.huecos.push(this.hueco_0);
      this.huecos.push(this.hueco_1);
      this.huecos.push(this.hueco_2);
      this.huecos.push(this.hueco_3);
      this.huecos.push(this.hueco_4);
      this.huecos.push(this.hueco_5);

      this.cantidad_huecos =
        this.hueco_0 +
        this.hueco_1 +
        this.hueco_2 +
        this.hueco_3 +
        this.hueco_4 +
        this.hueco_5;

      for (let i = 0; i < 6; i++) {
        this.valor_Ei[i] = (
          this.cantidad_huecos *
          (this.beta - this.alfa_rango) *
          Math.pow(1 - (this.beta - this.alfa_rango), i)
        ).toFixed(4);
        this.valor_error[i] = parseFloat(
          (
            Math.pow(this.valor_Ei[i] - this.huecos[i], 2) / this.valor_Ei[i]
          ).toFixed(4)
        );
      }

      this.error_total = parseFloat(
        this.valor_error
          .reduce((accumulator, currentValue) => accumulator + currentValue)
          .toFixed(4)
      );

      this.chi_cuadrada = parseFloat(
        formula.invChiSquareCDF(1 - this.alfa * 2, 5).toFixed(4)
      );
      return 0;
    },
    newCalc() {
      this.list_num_random = "";
      this.cantidad_num = 0;
      this.num_random = [];
      this.confianza = 95;
      this.valor_z = 1.96;
      this.alfa_rango = 0;
      this.beta = 0;
      this.alfa = 0;
      this.hueco_0 = 0;
      this.hueco_1 = 0;
      this.hueco_2 = 0;
      this.hueco_3 = 0;
      this.hueco_4 = 0;
      this.hueco_5 = 0;
      this.huecos = [];
      this.cantidad_huecos = 0;
      this.valor_Ei = [];
      this.valor_error = [];
      this.error_total = [];
      this.valores_S = [];
      this.chi_cuadrada = 0;
      this.show = false;
      this.show_error = 0;
    }
  }
};
</script>

