<template>
  <div class="hello">
    <h1 class="title">Prueba de Uniformidad Chi Cuadrada</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad numeros aleatorios</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad_num" :disabled="true" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Numeros pseudoaleatorios (ingresar numeros separados por ,)</p>
      </div>
      <div class="column">
        <textarea
          class="textarea has-fixed-size"
          placeholder="1, 2, 3, 4, ...."
          v-model="list_num_random"
          :disabled="show"
        ></textarea>
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Confianza</p>
      </div>
      <div class="column">
        <div class="control">
          <div class="select">
            <select v-model.number="confianza" :disabled="show">
              <option>95</option>
              <option>99</option>
              <option>90</option>
              <option>98</option>
              <option>80</option>
            </select>
          </div>
        </div>
        <!-- <input class="input" type="number" v-model.number="confianza" :disabled="true" /> -->
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="show">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>
    <div class="columns is-gapless results" v-if="show && !show_error">
      <div class="column is-1"></div>
      <div class="column is-2">
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>X</th>
              <th>Numero</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(element, index) in num_random" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ element }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-1"></div>
      <div class="column">
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor N</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="valor_n" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor M</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="valor_m" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor Ei</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="valor_ei" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor Rango</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="rango" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column">
            <table class="table is-hoverable">
              <thead>
                <tr>
                  <th>Rango Inf.</th>
                  <th>Rango Sup.</th>
                  <th>Oi</th>
                  <th>X^2</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(element, index) in rangos" :key="index">
                  <td>{{ element.valor_inf }}</td>
                  <td>{{ element.valor_sup }}</td>
                  <td>{{valores_Oi[index]}}</td>
                  <td>{{valores_X2[index]}}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor X^2</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="total_X2" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor Chi^2</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="chi_2" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Ho</p>
          </div>
          <div class="column">
            <p>ri ~U(0,1)</p>
            <p>X^2 &#60; Chi^2</p>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Hi</p>
          </div>
          <div class="column">
            <p>ri no son uniformes</p>
            <p>X^2 &#62; Chi^2</p>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Conclusion</p>
          </div>
          <div class="column">
            <p v-if="total_X2 < chi_2 ">{{ total_X2 }} &#60; {{ chi_2 }}</p>
            <p v-if="total_X2 < chi_2">No se rechaza Ho</p>
            <p v-if="total_X2 > chi_2">{{ total_X2 }} &#62; {{ chi_2 }}</p>
            <p v-if="total_X2 > chi_2">Se rechaza Ho</p>
          </div>
        </div>
      </div>
      <div class="column is-1"></div>
    </div>
    <div class="columns is-gapless results" v-if="show_error">
      <div class="column is-3"></div>
      <div class="column">
        <h1 class="title">Error en los datos, puede ser por:</h1>
        <div class="content is-medium">
          <ul>
            <li>Numeros mayores de 1</li>
            <li>Mal formato de numeros</li>
          </ul>
        </div>
      </div>
      <div class="column is-3"></div>
    </div>
  </div>
</template>

<script>
import formula from "inv-chisquare-cdf";
export default {
  name: "Algo_lineal",
  props: {
    msg: String
  },
  data() {
    return {
      list_num_random: "",
      cantidad_num: 0,
      num_random: [],
      valor_n: 0,
      valor_m: 0,
      rango: 0,
      confianza: 95,
      valor_ei: 0,
      total_X2: 0,
      rangos: [],
      valores_Oi: [],
      valores_X2: [],
      chi_2: 0,
      alfa: 0,
      show: false,
      show_error: false
    };
  },
  computed: {},

  methods: {
    calc() {
      this.show = true;

      this.num_random = this.list_num_random.split(",").map(Number);
      this.cantidad_num = this.num_random.length;

      for (let i = 0; i < this.cantidad_num; i++) {
        if (isNaN(this.num_random[i])) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
        if (this.num_random[i] > 1) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
      }

      this.valor_n = this.cantidad_num;
      this.valor_m = parseInt(Math.sqrt(this.valor_n));
      this.valor_ei = parseInt(this.valor_n / this.valor_m);
      this.rango = parseFloat((1 / this.valor_ei).toFixed(4));

      let inicio = 0;
      let fin = this.rango;

      for (let i = 0; i < this.valor_m; i++) {
        var new_rango = { valor_inf: inicio, valor_sup: fin };
        this.rangos.push(new_rango);
        let count = 0;
        for (let i = 0; i < this.cantidad_num; i++) {
          if (
            this.num_random[i] > new_rango.valor_inf &&
            this.num_random[i] < new_rango.valor_sup
          ) {
            count++;
          }
        }
        this.valores_Oi.push(count);
        inicio = parseFloat((inicio + this.rango).toFixed(4));
        fin = parseFloat((fin + this.rango).toFixed(4));
      }

      for (let i = 0; i < this.valor_m; i++) {
        var value = parseFloat(
          Math.pow(this.valor_ei - this.valores_Oi[i], 2) / this.valor_ei
        ).toFixed(4);
        this.valores_X2.push(value);
        this.total_X2 = parseFloat(
          (this.total_X2 + parseFloat(value)).toFixed(4)
        );
      }

      this.alfa = (1 - this.confianza / 100).toFixed(4);
      this.chi_2 = formula
        .invChiSquareCDF(1 - this.alfa, this.valor_ei - 1)
        .toFixed(4);

      return 0;
    },
    newCalc() {
      this.cantidad_num = 0;
      this.num_random = [];
      this.valor_n = 0;
      this.valor_m = 0;
      this.rango = 0;
      this.confianza = 95;
      this.valor_ei = 0;
      this.total_X2 = 0;
      this.rangos = [];
      this.valores_Oi = [];
      this.valores_X2 = [];
      this.chi_2 = 0;
      this.alfa = 0;
      this.show = false;
      this.show_error = false;
      this.list_num_random = "";
    }
  }
};
</script>

