<template>
  <div class="hello">
    <h1 class="title">Convolucion - Erlang</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad piezas</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad_piezas" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Media</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="media" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p># Erlang</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="num_erlang" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="show">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>

    <div class="columns is-gapless results" v-if="show">
      <div class="column is-1"></div>
      <div class="column">
        <div class="columns is-gapless is-vcentered">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>Formula Erlang</p>
          </div>
          <div class="column">
            <img src="../../assets/erlang/formula.png" />
          </div>
          <div class="column is-2"></div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-1"></div>
          <div class="column">
            <table class="table is-hoverable">
              <thead>
                <tr>
                  <th>Pieza</th>
                  <th v-for="(index) in num_erlang" :key="index">1- Ri</th>
                  <th>Y</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(index) in cantidad_piezas" :key="index">
                  <td>{{ index }}</td>
                  <td
                    v-for="(index_erlang) in num_erlang"
                    :key="index_erlang"
                  >{{num_random[index_erlang-1][index-1]}}</td>
                  <td>{{ valores_Xi[index-1] }}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="column is-1"></div>
        </div>
      </div>
      <div class="column is-1"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Erlang",
  props: {
    msg: String
  },
  data() {
    return {
      cantidad_piezas: 0,
      num_random: [],
      valores_Xi: [],
      media: 0,
      num_erlang: 0,
      show: false
    };
  },
  computed: {},

  methods: {
    calc() {
      this.show = true;

      for (let i = 0; i < this.num_erlang; i++) {
        this.num_random.push([]);
        for (let e = 0; e < this.cantidad_piezas; e++) {
          this.num_random[i][e] = parseFloat(
            (1 - (Math.random() * (0.9999 - 0.0001) + 0.0001)).toFixed(4)
          );
        }
      }

      let multi = 1;
      for (let i = 0; i < this.cantidad_piezas; i++) {
        for (let j = 0; j < this.num_erlang; j++) {
          console.log("multitplicador");
          console.log(this.num_random[j][i]);
          multi *= this.num_random[j][i];
        }
        let value = ((-this.media / this.num_erlang) * Math.log(multi)).toFixed(
          4
        );
        multi = 1;
        this.valores_Xi.push(value);
      }

      return 0;
    },

    newCalc() {
      this.show = false;
      this.cantidad_piezas = 0;
      this.num_random = [];
      this.valores_Xi = [];
      this.media = 0;
      this.tiempo = 0;
      this.acumulado = [];
      this.num_erlang = 0;
    }
  }
};
</script>

