<template>
  <div class="hello">
    <h1 class="title">Productos Medios</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Semilla 1</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="semilla1" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Semilla 2</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="semilla2" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Repeticiones</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="repeticiones" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>
    <div class="columns is-gapless">
      <div class="column is-2"></div>
      <div class="column">
        <table class="table is-hoverable" v-if="show">
          <thead>
            <tr>
              <th>N</th>
              <th>Semilla 1</th>
              <th>Semilla 2</th>
              <th>Y</th>
              <th>X</th>
              <th>R</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="index in repeticiones" :key="index">
              <td>{{ index }}</td>
              <td>{{ array_semilla_1[index - 1] }}</td>
              <td>{{ array_semilla_2[index - 1] }}</td>
              <td>{{ array_Y[index - 1] }}</td>
              <td>{{ array_X[index - 1] }}</td>
              <td>{{ array_R[index - 1] }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-2"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Prodcutos_Medios",
  props: {
    msg: String
  },
  data() {
    return {
      semilla1: 0,
      semilla2: 0,
      repeticiones: 1,
      array_semilla_1: [],
      array_semilla_2: [],
      array_Y: [],
      array_X: [],
      array_R: [],
      show: false
    };
  },
  methods: {
    calc() {
      this.show = true;
      this.array_semilla_1[0] = this.semilla1;
      this.array_semilla_2[0] = this.semilla2;
      this.array_Y[0] = this.array_semilla_1[0] * this.array_semilla_2[0];
      let inicio =
        (this.array_Y[0].toString().length - this.semilla1.toString().length) /
        2;
      let final = inicio + this.semilla1.toString().length;

      this.array_X[0] = parseInt(
        this.array_Y[0].toString().substring(inicio, final)
      );
      this.array_R[0] =
        this.array_X[0] / Math.pow(10, this.semilla1.toString().length);

      for (var i = 1; i < this.repeticiones; i++) {
        this.array_semilla_1[i] = this.array_semilla_2[i - 1];
        this.array_semilla_2[i] = this.array_X[i - 1];
        this.array_Y[i] = this.array_semilla_1[i] * this.array_semilla_2[i];
        inicio =
          (this.array_Y[i].toString().length -
            this.semilla1.toString().length) /
          2;
        final = inicio + this.semilla1.toString().length;
        this.array_X[i] = parseInt(
          this.array_Y[i].toString().substring(inicio, final)
        );
        this.array_R[i] =
          this.array_X[i] / Math.pow(10, this.semilla1.toString().length);
      }

      return 0;
    },
    newCalc() {
      this.show = false;
      this.semilla1 = 0;
      this.semilla2 = 0;
      this.repeticiones = 1;
      this.array_X = [];
      this.array_R = [];
      this.array_Y = [];
      this.array_semilla_1 = [];
      this.array_semilla_2 = [];
    }
  }
};
</script>

