<template>
  <div class="hello">
    <h1 class="title">Convolucion - Binomial</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Probabilidad de error (0-1)</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="probabilidad" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="show || valid_data">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>

    <div class="columns is-gapless results" v-if="show">
      <div class="column is-1"></div>
      <div class="column">
        <div class="columns is-gapless is-vcentered">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>Formula Binomial</p>
          </div>
          <div class="column">
            <img src="../../assets/binomial/formula.png" />
          </div>
          <div class="column is-2"></div>
        </div>
        <div class="columns is-gapless">
          <div class="column">
            <div class="columns is-gapless">
              <div class="column is-1"></div>
              <div class="column is-3">
                <p>SIN Error = 0</p>
              </div>
              <div class="column">
                <input class="input" type="text" v-model="mensaje_0" :disabled="show" />
              </div>
              <div class="column is-2"></div>
            </div>
            <div class="columns is-gapless">
              <div class="column is-1"></div>
              <div class="column is-3">
                <p>CON Error = 1</p>
              </div>
              <div class="column">
                <input class="input" type="text" v-model="mensaje_1" :disabled="show" />
              </div>
              <div class="column is-2"></div>
            </div>

            <download-csv :data="json_data" name="tabla_resultados_binomial.csv">
              <button class="button is-primary">Descargar Tabla de Resultados</button>
            </download-csv>
          </div>
          <div class="column is-1"></div>
        </div>
      </div>
      <div class="column is-1"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Binomial",
  props: {
    msg: String
  },
  data() {
    return {
      cantidad: 0,
      num_random: [],
      probabilidad: 0,
      valores_Xi: [],
      error: 0,
      mensaje_0: "",
      mensaje_1: "",
      valores_df: [],
      show: false,
      json_data: []
    };
  },
  computed: {
    valid_data() {
      if (this.probabilidad >= 0 && this.probabilidad <= 1) {
        return false;
      }
      return true;
    }
  },

  methods: {
    calc() {
      this.error = (1 - this.probabilidad).toFixed(4);
      this.mensaje_0 = `0 - ${this.error}`;
      this.mensaje_1 = `${this.error} - 1`;

      for (let i = 0; i < this.cantidad; i++) {
        this.num_random.push([]);
        this.valores_Xi.push([]);
        for (let e = 0; e < this.cantidad; e++) {
          this.num_random[i][e] = parseFloat(
            (1 - (Math.random() * (0.9999 - 0.0001) + 0.0001)).toFixed(4)
          );
        }
      }

      for (let i = 0; i < this.cantidad; i++) {
        for (let j = 0; j < this.cantidad; j++) {
          if (this.num_random[j][i] <= this.error) {
            this.valores_Xi[j][i] = 0;
          } else {
            this.valores_Xi[j][i] = 1;
          }
        }
      }

      for (let i = 0; i < this.cantidad; i++) {
        let count = 0;
        for (let j = 0; j < this.cantidad; j++) {
          if (this.valores_Xi[j][i] == 1) {
            count++;
          }
        }
        this.valores_df.push(count);
      }

      for (let i = 0; i < this.cantidad; i++) {
        let new_row = {};
        new_row.index = i + 1;
        for (let e = 0; e < this.cantidad; e++) {
          new_row["R" + (e + 1)] = this.num_random[e][i];
          new_row["BE" + (e + 1)] = this.valores_Xi[e][i];
        }
        new_row["Df"] = this.valores_df[i];
        this.json_data.push(new_row);

        if (i == this.cantidad - 1) {
          this.show = true;
        }
      }

      return 0;
    },

    newCalc() {
      this.show = false;
      this.cantidad = 0;
      this.num_random = [];
      this.valores_Xi = [];
      this.tiempo = 0;
      this.acumulado = [];
      this.probabilidad = 0;
      this.error = 0;
      this.mensaje_0 = "";
      this.mensaje_1 = "";
      this.valores_df = [];
      this.json_data = [];
    }
  }
};
</script>

