<template>
  <div class="hello">
    <h1 class="title">Convolucion - Normal</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad variables Aleatorias</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad_var" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Desviacion estandar (0-1)</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="desviacion_estandar" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Media</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="media" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="show || valid_data">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>

    <div class="columns is-gapless results" v-if="show">
      <div class="column is-1"></div>
      <div class="column">
        <div class="columns is-gapless is-vcentered">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>Formula Normal</p>
          </div>
          <div class="column">
            <img src="../../assets/con_normal/formula.png" />
          </div>
          <div class="column is-2"></div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-1"></div>
          <div class="column is-2">
            <table class="table is-hoverable">
              <thead>
                <tr>
                  <th>X</th>
                  <th>Ri</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(element,index) in num_random" :key="index">
                  <td>{{ index+1 }}</td>
                  <td>{{element}}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="column is-1"></div>
          <div class="column">
            <table class="table is-hoverable">
              <thead>
                <tr>
                  <th>Numero</th>
                  <th>Suma Ri</th>
                  <th>6 - Ri</th>
                  <th>Ni</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(index) in cantidad_var" :key="index">
                  <td>{{ index }}</td>
                  <td>{{suma_r[index-1]}}</td>
                  <td>{{resta_r[index-1]}}</td>
                  <td>{{valores_Xi[index-1]}}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="column is-1"></div>
        </div>
      </div>
      <div class="column is-1"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Binomial",
  props: {
    msg: String
  },
  data() {
    return {
      cantidad_var: 0,
      num_random: [],
      desviacion_estandar: 0,
      valores_Xi: [],
      suma_r: [],
      resta_r: [],
      media: 0,
      show: false
    };
  },
  computed: {
    valid_data() {
      if (this.desviacion_estandar <= 1 && this.desviacion_estandar >= 0) {
        return false;
      }
      return true;
    }
  },

  methods: {
    calc() {
      this.show = true;

      for (let i = 0; i < this.cantidad_var * 12; i++) {
        this.num_random[i] = parseFloat(
          (1 - (Math.random() * (0.9999 - 0.0001) + 0.0001)).toFixed(4)
        );
      }

      for (let i = 0; i < this.cantidad_var * 12; i += 12) {
        let count = 0;
        for (let e = 0; e < 12; e++) {
          count += this.num_random[i + e];
        }
        this.suma_r.push(count.toFixed(4));
        this.resta_r.push((count - 6).toFixed(4));
      }

      for (let i = 0; i < this.cantidad_var; i++) {
        this.valores_Xi[i] =
          parseFloat(this.resta_r[i]) + this.desviacion_estandar + this.media;
      }

      return 0;
    },

    newCalc() {
      this.show = false;
      this.cantidad_var = 0;
      this.num_random = [];
      this.valores_Xi = [];
      this.desviacion_estandar = 0;
      this.media = 0;
      this.suma_r = [];
      this.resta_r = [];
    }
  }
};
</script>

