<template>
  <div class="hello">
    <h1 class="title">Prueba de Medias</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad numeros aleatorios</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad_num" :disabled="true" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Numeros pseudoaleatorios (ingresar numeros separados por ,)</p>
      </div>
      <div class="column">
        <textarea
          class="textarea has-fixed-size"
          placeholder="1, 2, 3, 4, ...."
          v-model="list_num_random"
          :disabled="show"
        ></textarea>
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Confianza</p>
      </div>
      <div class="column">
        <div class="control">
          <div class="select">
            <select v-model.number="confianza" :disabled="show">
              <option>95</option>
              <option>99</option>
              <option>90</option>
              <option>98</option>
              <option>80</option>
            </select>
          </div>
        </div>
        <!-- <input class="input" type="number" v-model.number="confianza" :disabled="true" /> -->
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="show">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>
    <div class="columns is-gapless results" v-if="show  && !show_error">
      <div class="column is-1"></div>
      <div class="column is-2">
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>X</th>
              <th>Numero</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(element, index) in num_random" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ element }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-1"></div>
      <div class="column">
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Valor ri</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="valor_ri" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Alfa</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="alfa" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Limite Inferior</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="limiteInf" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Limite Superior</p>
          </div>
          <div class="column">
            <input class="input" type="number" v-model.number="limiteSup" :disabled="true" />
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Ho</p>
          </div>
          <div class="column">
            <p>Lim. Inferior &#60; ri &#60; Lim. Superior</p>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Hi</p>
          </div>
          <div class="column">
            <p>
              ri
              <b>&#60;</b> Lim. Inferior
            </p>
            <p>
              ri
              <b>&#62;</b> Lim. Superior
            </p>
          </div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-3">
            <p>Conclusion</p>
          </div>
          <div class="column">
            <p
              v-if="valor_ri < limiteSup && valor_ri > limiteInf"
            >{{ limiteInf }} &#60; {{ valor_ri }} &#60; {{ limiteSup }}</p>
            <p v-if="valor_ri < limiteSup && valor_ri > limiteInf">No se rechaza Ho</p>
            <p v-if="valor_ri > limiteSup">{{ valor_ri }} &#62; {{ limiteSup }}</p>
            <p v-if="valor_ri > limiteSup">Se rechaza Ho</p>
            <p v-if="valor_ri < limiteInf">{{ valor_ri }} &#60; {{ limiteInf }}</p>
            <p v-if="valor_ri < limiteInf">Se rechaza Ho</p>
          </div>
        </div>
      </div>
      <div class="column is-1"></div>
    </div>
    <div class="columns is-gapless results" v-if="show_error">
      <div class="column is-3"></div>
      <div class="column">
        <h1 class="title">Error en los datos, puede ser por:</h1>
        <div class="content is-medium">
          <ul>
            <li>Numeros mayores de 1</li>
            <li>Mal formato de numeros</li>
          </ul>
        </div>
      </div>
      <div class="column is-3"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Algo_lineal",
  props: {
    msg: String
  },
  data() {
    return {
      list_num_random: "",
      cantidad_num: 0,
      num_random: [],
      valor_ri: 0,
      confianza: 95,
      valor_z: 1.96,
      alfa: 0,
      limiteInf: 0,
      limiteSup: 0,
      show: false,
      show_error: false
    };
  },
  computed: {},

  methods: {
    calc() {
      this.show = true;
      this.num_random = this.list_num_random.split(",").map(Number);
      this.cantidad_num = this.num_random.length;

      for (let i = 0; i < this.cantidad_num; i++) {
        if (isNaN(this.num_random[i])) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
        if (this.num_random[i] > 1) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
      }

      this.valor_ri = (
        this.num_random.reduce((previous, current) => (current += previous)) /
        this.cantidad_num
      ).toFixed(4);

      switch (this.confianza) {
        case 99:
          this.valor_z = 2.58;
          this.alfa = 0.005;
          break;
        case 90:
          this.valor_z = 1.645;
          this.alfa = 0.05;
          break;
        case 98:
          this.valor_z = 2.33;
          this.alfa = 0.01;
          break;
        case 80:
          this.valor_z = 1.28;
          this.alfa = 0.1;
          break;

        default:
          this.valor_z = 1.96;
          this.alfa = 0.025;
      }

      this.limiteInf = (
        0.5 -
        this.valor_z * (1 / Math.sqrt(12 * this.cantidad_num))
      ).toFixed(4);

      this.limiteSup = (
        0.5 +
        this.valor_z * (1 / Math.sqrt(12 * this.cantidad_num))
      ).toFixed(4);

      return 0;
    },
    newCalc() {
      this.show = false;
      this.cantidad_num = 0;
      this.num_random = [];
      this.list_num_random = "";
      this.valor_ri = 0;
      this.confianza = 95;
      this.valor_z = 1.96;
      this.alfa = 0;
      this.limiteInf = 0;
      this.limiteSup = 0;
      this.show_error = false;
    }
  }
};
</script>

