<template>
  <div class="hello">
    <h1 class="title">Congruencial Aditivo</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Numero de Entradas</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="num_entradas" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Modulo</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="modulo" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Repeticiones</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="repeticiones" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div>
      <div class="columns is-gapless" v-for="index in num_entradas" :key="index">
        <div class="column is-1"></div>
        <div class="column is-3">
          <p>X-{{index}}</p>
        </div>
        <div class="column">
          <input class="input" type="number" v-model.number="array_X[index-1]" :disabled="show" />
        </div>
        <div class="column is-2"></div>
      </div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="valid_data">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>
    <div class="columns is-gapless">
      <div class="column is-4"></div>
      <div class="column">
        <table class="table is-hoverable" v-if="show">
          <thead>
            <tr>
              <th>N</th>
              <th>X</th>
              <th>R</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="index in repeticiones+num_entradas" :key="index">
              <td>{{ index }}</td>
              <td>{{ array_X[index - 1] }}</td>
              <td>{{ array_R[index - 1].toFixed(4) }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-4"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Cong_aditivo",
  props: {
    msg: String
  },
  data() {
    return {
      num_entradas: 0,
      modulo: 0,
      repeticiones: 0,
      array_X: [],
      array_R: [],
      show: false
    };
  },
  computed: {
    valid_data() {
      if (this.num_entradas > 4) {
        return false;
      } else {
        return true;
      }
    }
  },

  methods: {
    calc() {
      this.show = true;
      for (var e = 0; e < this.num_entradas; e++) {
        this.array_R[e] = 0;
      }
      this.array_X[this.num_entradas] =
        (this.array_X[0] + this.array_X[this.num_entradas - 1]) % this.modulo;
      this.array_R[this.num_entradas] =
        this.array_X[this.num_entradas] / (this.modulo - 1);

      for (var i = 1; i < this.repeticiones; i++) {
        console.log("repeticiones");
        this.array_X[this.num_entradas + i] =
          (this.array_X[i] + this.array_X[this.num_entradas + i - 1]) %
          this.modulo;
        this.array_R[this.num_entradas + i] =
          this.array_X[this.num_entradas + i] / (this.modulo - 1);
      }
      return 0;
    },
    newCalc() {
      this.show = false;
      this.num_entradas = 0;
      this.modulo = 0;
      this.repeticiones = 0;
      this.array_X = [];
      this.array_R = [];
    }
  }
};
</script>

