<template>
  <div class="hello">
    <h1 class="title">Transformada Inversa - Uniforme</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad numeros aleatorios</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad_num" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Rango Inferior</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="rango_inf" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Rango Superior</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="rango_sup" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="show || valid_data">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>

    <div class="columns is-gapless results" v-if="show">
      <div class="column is-1"></div>
      <div class="column">
        <div class="columns is-gapless is-vcentered">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>Distribucion uniforme</p>
          </div>
          <div class="column">
            <img src="../../assets/Uniforme/dis_uni_form.png" />
          </div>
          <div class="column is-2"></div>
        </div>
        <div class="columns is-gapless is-vcentered">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>Distribucion uniforme acumulado</p>
          </div>
          <div class="column">
            <img src="../../assets/Uniforme/dis_uni_acum.png" />
          </div>
          <div class="column is-2"></div>
        </div>
        <div class="columns is-gapless is-vcentered">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>Distribucion uniforme Xi</p>
          </div>
          <div class="column">
            <img src="../../assets/Uniforme/dis_uni_x.png" />
          </div>
          <div class="column is-2"></div>
        </div>
        <table class="table is-hoverable">
          <thead>
            <tr>
              <th>Medición</th>
              <th>Ri</th>
              <th>Xi</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(element, index) in num_random" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ element }}</td>
              <td>{{ valores_Xi[index] }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-1"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Uniforme",
  props: {
    msg: String
  },
  data() {
    return {
      cantidad_num: 0,
      num_random: [],
      valores_Xi: [],
      rango_inf: 0,
      rango_sup: 0,
      show: false
    };
  },
  computed: {
    valid_data() {
      if (this.rango_inf < this.rango_sup) {
        return false;
      }
      return true;
    }
  },

  methods: {
    calc() {
      this.show = true;

      for (let i = 0; i < this.cantidad_num; i++) {
        this.num_random[i] = parseFloat(
          (Math.random() * (0.9999 - 0.0001) + 0.0001).toFixed(4)
        );
        this.valores_Xi[i] = (
          this.rango_inf +
          (this.rango_sup - this.rango_inf) * this.num_random[i]
        ).toFixed(4);
      }

      return 0;
    },
    newCalc() {
      this.show = false;
      this.cantidad_num = 0;
      this.num_random = [];
      this.valores_Xi = [];
      this.rango_inf = 0;
      this.rango_sup = 0;
    }
  }
};
</script>

