<script>
// import formula from "inv-chisquare-cdf";
export default {
  name: "Kolmgorov_Smirnov",
  props: {
    msg: String
  },
  data() {
    return {
      list_num_random: "",
      // cantidad de numeros agregados
      cantidad_num: 0,
      // variable para guardar los numeros random
      num_random: [],
      // valor de la confienza, se inicializa en 95, pero puede cambiar
      confianza: 95,
      // valor del primer renglon de la tabla
      valor_1: [],
      // valor del tercer renglon en la tabla
      valor_3: [],
      // valor del cuarto renglon en la tabla
      valor_4: [],
      // valor del quinto renglon en la tabla
      valor_5: [],
      // variable para guardar el valor maximo dentro del cuarto renglon
      d_max: 0,
      // variable para guardar el valor minimo dentro del quinto renglon
      d_min: 0,
      // valor de D calculado con d_max y d_min
      d_value: 0,
      // valor de D segun la tabla
      d_table: 0,
      alfa: 0,
      // valores de la tabla donde se busca el valor de D
      table: [],
      show: false,
      show_error: false
    };
  },
  methods: {
    calc() {
      this.show = true;
      this.num_random = this.list_num_random.split(",").map(Number);

      this.cantidad_num = this.num_random.length;
      if (this.cantidad_num > 20) {
        console.log("error");
        this.show_error = true;
        return 0;
      }
      for (let i = 0; i < this.cantidad_num; i++) {
        if (isNaN(this.num_random[i])) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
        if (this.num_random[i] > 1) {
          console.log("error");
          this.show_error = true;
          return 0;
        }
      }

      // se ordena el arreglo de los numeros aleatorios
      this.num_random.sort((a, b) => a - b);

      // se calculan los valores dentro de la tabla
      for (let i = 0; i < this.cantidad_num; i++) {
        this.valor_1[i] = parseFloat(((i + 1) / this.cantidad_num).toFixed(4));
        this.valor_3[i] = parseFloat((i / this.cantidad_num).toFixed(4));
        this.valor_4[i] = parseFloat(
          (this.valor_1[i] - this.num_random[i]).toFixed(4)
        );
        this.valor_5[i] = parseFloat(
          (this.num_random[i] - this.valor_3[i]).toFixed(4)
        );
      }
      // se encuentra el maxico y minido de las columnas 4 y 5
      this.d_max = Math.max(...this.valor_4);
      this.d_min = Math.abs(Math.min(...this.valor_5));
      // se asigna el mayor al valor de D entre d_min y d_max
      this.d_value = this.d_max > this.d_min ? this.d_max : this.d_min;
      this.alfa = parseFloat((1 - this.confianza / 100).toFixed(4));

      // se llena la tabla, no encontre una libreria para sacar estos datos, cree la tabla
      // esta tabla solo funciona para niveles de confianza de 95, 99, 90, 98 y 80
      // el profe me comento que no habia problema con solo usar esos niveles de confianza
      this.table[0] = [0.995, 0.99, 0.975, 0.95, 0.9];
      this.table[1] = [0.9293, 0.9, 0.8418, 0.7763, 0.6837];
      this.table[2] = [0.829, 0.7845, 0.7076, 0.636, 0.5648];
      this.table[3] = [0.7342, 0.6888, 0.6239, 0.5652, 0.4926];
      this.table[4] = [0.6685, 0.6271, 0.5632, 0.5094, 0.4469];
      this.table[5] = [0.6166, 0.5774, 0.5192, 0.4679, 0.4103];
      this.table[6] = [0.5758, 0.5384, 0.4834, 0.436, 0.3814];
      this.table[7] = [0.5418, 0.5065, 0.4542, 0.4096, 0.3582];
      this.table[8] = [0.5133, 0.4796, 0.43, 0.3874, 0.339];
      this.table[9] = [0.4889, 0.4566, 0.4092, 0.3686, 0.3225];
      this.table[10] = [0.4677, 0.4367, 0.3912, 0.3524, 0.3082];
      this.table[11] = [0.449, 0.4191, 0.3754, 0.3381, 0.2957];
      this.table[12] = [0.4324, 0.4036, 0.3614, 0.3254, 0.2846];
      this.table[13] = [0.4176, 0.3897, 0.3448, 0.3141, 0.2747];
      this.table[14] = [0.4042, 0.3771, 0.3376, 0.3039, 0.2658];
      this.table[15] = [0.392, 0.3657, 0.3283, 0.2947, 0.2577];
      this.table[16] = [0.3808, 0.3552, 0.3179, 0.2862, 0.2503];
      this.table[17] = [0.3706, 0.3456, 0.3093, 0.2785, 0.2435];
      this.table[18] = [0.3611, 0.3368, 0.3014, 0.2713, 0.2435];
      this.table[19] = [0.3524, 0.3286, 0.294, 0.2647, 0.2315];

      // se saca el valor de la tabla
      switch (this.alfa) {
        case 0.01:
          this.d_table = this.table[this.cantidad_num - 1][0];
          break;
        case 0.02:
          this.d_table = this.table[this.cantidad_num - 1][1];
          break;
        case 0.05:
          this.d_table = this.table[this.cantidad_num - 1][2];
          break;
        case 0.1:
          this.d_table = this.table[this.cantidad_num - 1][3];
          break;
        case 0.2:
          this.d_table = this.table[this.cantidad_num - 1][4];
          break;
        default:
          this.d_table = 0;
          break;
      }

      // para saber que no se rechaza Ho, d_tabla > d_value

      return 0;
    },
    newCalc() {
      this.show_error = false;
      this.show = false;
      this.cantidad_num = 0;
      this.rangos = [];
      this.num_random = [];
      this.list_num_random = "";
      this.valor_1 = [];
      this.valor_3 = [];
      this.valor_4 = [];
      this.valor_5 = [];
      this.d_max = 0;
      this.d_min = 0;
      this.d_value = 0;
      this.d_table = 0;
      this.table = [];
      this.alfa = 0;
    }
  }
};
</script>

