<template>
  <div class="hello">
    <h1 class="title">Congruencial Multiplicativo</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Semilla</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="semilla" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Multiplicador (a)</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="constante_multi" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Modulo (m)</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="modulo" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Repeticiones</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="repeticiones" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>
    <div class="columns is-gapless">
      <div class="column is-4"></div>
      <div class="column">
        <table class="table is-hoverable" v-if="show">
          <thead>
            <tr>
              <th>N</th>
              <th>X</th>
              <th>R</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="index in repeticiones" :key="index">
              <td>{{ index }}</td>
              <td>{{ array_X[index - 1] }}</td>
              <td>{{ array_R[index - 1].toFixed(4) }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="column is-4"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Cong_multi",
  props: {
    msg: String
  },
  data() {
    return {
      semilla: 0,
      constante_multi: 0,
      constante_aditiva: 0,
      modulo: 0,
      repeticiones: 1,
      array_X: [],
      array_R: [],
      show: false
    };
  },
  computed: {},

  methods: {
    calc() {
      this.show = true;

      this.array_X[0] =
        (this.constante_multi * this.semilla + this.constante_aditiva) %
        this.modulo;
      this.array_R[0] = this.array_X[0] / (this.modulo - 1);
      for (var i = 1; i < this.repeticiones; i++) {
        this.array_X[i] =
          (this.constante_multi * this.array_X[i - 1] +
            this.constante_aditiva) %
          this.modulo;
        this.array_R[i] = this.array_X[i] / (this.modulo - 1);
      }
      return 0;
    },
    newCalc() {
      this.show = false;
      this.semilla = 0;
      this.constante_multi = 0;
      this.constante_aditiva = 0;
      this.modulo = 0;
      this.repeticiones = 0;
      this.array_X = [];
      this.array_R = [];
    }
  }
};
</script>

