<template>
  <div class="hello">
    <h1 class="title">Transformada Inversa - Poisson</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad piezas</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad_piezas" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Media</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="media" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Tiempo</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="tiempo" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="show">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>

    <div class="columns is-gapless results" v-if="show">
      <div class="column is-1"></div>
      <div class="column">
        <div class="columns is-gapless is-vcentered">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>Distribucion Poisson</p>
          </div>
          <div class="column">
            <img src="../../assets/poisson/formula.png" />
          </div>
          <div class="column is-2"></div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-1"></div>
          <div class="column">
            <table class="table is-hoverable">
              <thead>
                <tr>
                  <th>X</th>
                  <th>P(x)</th>
                  <th>Acumulado P(x)</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(element, index) in cantidad_piezas+1" :key="index">
                  <td>{{ index }}</td>
                  <td>{{ num_P[index] }}</td>
                  <td>{{ acumulado[index] }}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="column is-1"></div>
          <div class="column">
            <table class="table is-hoverable">
              <thead>
                <tr>
                  <th>Hora</th>
                  <th>Ri</th>
                  <th>Xi</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(index) in tiempo" :key="index">
                  <td>{{ index }}</td>
                  <td>{{ num_random[index-1] }}</td>
                  <td>{{ valores_Xi[index-1] }}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="column is-2"></div>
        </div>
      </div>
      <div class="column is-1"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Poisson",
  props: {
    msg: String
  },
  data() {
    return {
      cantidad_piezas: 0,
      rangos: [0],
      num_random: [],
      num_P: [],
      valores_Xi: [],
      media: 0,
      tiempo: 0,
      acumulado: [],
      show: false
    };
  },
  computed: {},

  methods: {
    calc() {
      this.show = true;

      let count = 0;
      for (let i = 0; i <= this.cantidad_piezas; i++) {
        this.num_P[i] = parseFloat(
          (
            (Math.pow(this.media, i) * Math.exp(-this.media)) /
            this.factorialize(i)
          ).toFixed(5)
        );
        count = count + this.num_P[i];
        this.acumulado.push(parseFloat(count.toFixed(5)));
      }

      for (let i = 0; i < this.tiempo; i++) {
        this.num_random[i] = parseFloat(
          (Math.random() * (0.9999 - 0.0001) + 0.0001).toFixed(5)
        );
      }

      for (let i = 1; i <= this.acumulado.length; i++) {
        this.rangos[i] = this.acumulado[i - 1];
      }

      for (let i = 0; i < this.tiempo; i++) {
        for (let e = 1; e < this.num_P.length; e++) {
          if (this.num_random[i] <= this.acumulado[0]) {
            this.valores_Xi.push(0);
            e = this.num_P.length;
          } else if (
            this.num_random[i] >= this.acumulado[e - 1] &&
            this.num_random[i] <= this.acumulado[e]
          ) {
            this.valores_Xi.push(e);
            e = this.num_P.length;
          }
        }
      }

      return 0;
    },
    factorialize(num) {
      if (num === 0 || num === 1) return 1;
      for (var i = num - 1; i >= 1; i--) {
        num *= i;
      }
      return num;
    },
    newCalc() {
      this.show = false;
      this.cantidad_piezas = 0;
      this.num_random = [];
      this.num_P = [];
      this.valores_Xi = [];
      this.media = 0;
      this.tiempo = 0;
      this.acumulado = [];
    }
  }
};
</script>

