<template>
  <div class="hello">
    <h1 class="title">Transformada Inversa - Normal Estandar</h1>
    <div class="columns is-gapless">
      <div class="column is-1"></div>
      <div class="column is-3">
        <p>Cantidad numeros generados</p>
      </div>
      <div class="column">
        <input class="input" type="number" v-model.number="cantidad_num" :disabled="show" />
      </div>
      <div class="column is-2"></div>
    </div>

    <button class="button is-info" v-on:click="calc()" :disabled="show">Calc</button>
    <button class="button is-success" v-on:click="newCalc()">New Calc</button>

    <div class="columns is-gapless results" v-if="show">
      <div class="column is-1"></div>
      <div class="column">
        <div class="columns is-gapless is-vcentered">
          <div class="column is-1"></div>
          <div class="column is-3">
            <p>Formula Distribucion Normal Estandar</p>
          </div>
          <div class="column">
            <img src="../../assets/tranformada_normal_estandar/formula.png" />
          </div>
          <div class="column is-2"></div>
        </div>
        <div class="columns is-gapless">
          <div class="column is-1"></div>
          <div class="column">
            <table class="table is-hoverable">
              <thead>
                <tr>
                  <th>X</th>
                  <th>P(z) Acumulado</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(element, index) in num_P" :key="index">
                  <td>{{ num_X[index] }}</td>
                  <td>{{element}}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="column is-1"></div>
          <div class="column">
            <table class="table is-hoverable">
              <thead>
                <tr>
                  <th>X</th>
                  <th>Rango Inferior</th>
                  <th>Rango Superior</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(element, index) in num_P" :key="index">
                  <td>{{ num_X[index] }}</td>
                  <td>{{intervalos[index].inicio}}</td>
                  <td>{{intervalos[index].fin}}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="column is-1"></div>
          <div class="column">
            <table class="table is-hoverable">
              <thead>
                <tr>
                  <th>#</th>
                  <th>Num. Aleatorio</th>
                  <th>Intervalo</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(index) in cantidad_num" :key="index">
                  <td>{{ index }}</td>
                  <td>{{ num_random[index-1] }}</td>
                  <td>{{ num_intervalo[index-1] }}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="column is-1"></div>
        </div>
      </div>
      <div class="column is-1"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Transformada_Normal",
  props: {
    msg: String
  },
  data() {
    return {
      cantidad_num: 0,
      num_random: [],
      num_P: [],
      num_X: [],
      intervalos: [],
      num_intervalo: [],
      rango_inf: [],
      rangop_sup: [],
      show: false
    };
  },
  computed: {},

  methods: {
    calc() {
      this.show = true;

      this.num_P[0] = 0.0013499;
      this.num_P[1] = 0.02275013;
      this.num_P[2] = 0.15865525;
      this.num_P[3] = 0.5;
      this.num_P[4] = 0.84134475;
      this.num_P[5] = 0.97724987;
      this.num_P[6] = 1;

      this.num_X = [-3, -2, -1, 0, 1, 2, 3];

      this.intervalos[0] = { inicio: 0, fin: 0.0013499 };
      this.intervalos[1] = { inicio: 0.0013499, fin: 0.02275013 };
      this.intervalos[2] = { inicio: 0.02275013, fin: 0.15865525 };
      this.intervalos[3] = { inicio: 0.15865525, fin: 0.5 };
      this.intervalos[4] = { inicio: 0.5, fin: 0.84134475 };
      this.intervalos[5] = { inicio: 0.84134475, fin: 0.97724987 };
      this.intervalos[6] = { inicio: 0.97724987, fin: 1 };

      for (let i = 0; i < this.cantidad_num; i++) {
        this.num_random[i] = parseFloat(
          (Math.random() * (0.9999 - 0.0001) + 0.0001).toFixed(4)
        );
      }

      for (let i = 0; i < this.cantidad_num; i++) {
        for (let e = 0; e < this.intervalos.length; e++) {
          if (
            this.num_random[i] > this.intervalos[e].inicio &&
            this.num_random[i] < this.intervalos[e].fin
          ) {
            this.num_intervalo.push(this.num_X[e]);
          }
        }
      }

      return 0;
    },
    newCalc() {
      this.show = false;
      this.cantidad_num = 0;
      this.num_random = [];
      this.num_P = [];
      this.intervalos = [];
      this.num_intervalo = [];
    }
  }
};
</script>

